.row{
    margin-left: 20px;
    color: #fff;
}

.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_poster{
    object-fit:contain;
    width:100%;
    max-width: 100px;
    margin-right: 10px;
    transition: all 450ms;
}

.row_posters::-webkit-scrollbar{
    display: none;
}

.row_poster:hover{
    transform: scale(1.08);
}

.row_poster_large{
    max-height: 250px;
}

.row_poster_large:hover{
    transform:scale(1.09);
}