.banner{
    color:white;
    object-fit: contain;
    height:448px;
}
.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height:190px;
}
.banner_title{
    font-size: 3rem;
    padding-bottom: .3rem;
    font-weight: 800;
}
.banner_description{
    max-width:45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px; 
}
.banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: .2vw;
    padding: .5rem 2rem 0.5rem 2rem;
    margin: 0 1rem 0 0;
    background-color: rgba(51,51,51,.6);
}
.banner_button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all .2s;
}
.banner_fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
    bottom: 0;
}